"use strict";
var promise_polyfill_1 = require("promise-polyfill");
if (!window['Promise'])
    window['Promise'] = promise_polyfill_1.Promise;
require("whatwg-fetch");
var trends_1 = require("./trends");
var content_1 = require("./content");
var specific_trends_1 = require("./specific-trends");
var specific_content_1 = require("./specific-content");
var news_1 = require("./news");
var tweet_1 = require("./tweet");
var APIURL = 'http://neptune.gunshippenguin.com:8080/v1';
var ENDPOINTS = {
    trends: '/trends',
    specificTrends: '/trends/{id}',
    content: '/content?page={page}',
    specificContent: '/content/{id}?page={page}'
};
/**
 * NetworkBus class handles all communications with the REST API.
 * @author Omar Chehab
 */
var NetworkBus = (function () {
    function NetworkBus() {
    }
    /**
     * Requests and parses the trends from the REST API.
     * @author Omar Chehab
     */
    NetworkBus.getTrends = function (callback) {
        var endpoint = ENDPOINTS.trends;
        var url = "" + APIURL + endpoint;
        window['fetch'](url)
            .then(function (response) {
            return response.json();
        }, function (error) {
            callback(error, undefined);
        })
            .then(function (response) {
            response = new trends_1.Trends(response);
            callback(undefined, response);
        });
        console.log('getTrends', url);
    };
    /**
     * Requests and parses the content from the REST API.
     * @author Omar Chehab
     */
    NetworkBus.getContent = function (callback, page) {
        var endpoint = ENDPOINTS.content
            .replace(/{page}/, "" + page);
        var url = "" + APIURL + endpoint;
        window['fetch'](url)
            .then(function (response) {
            return response.json();
        }, function (error) {
            callback(error, undefined);
        })
            .then(function (response) {
            response.news = response.news.map(function (news) { return new news_1.News(news); });
            response.tweets = response.tweets.map(function (tweet) { return new tweet_1.Tweet(tweet); });
            response = new content_1.Content(response);
            callback(undefined, response);
        });
        console.log('getContent', url);
    };
    /**
     * Requests and parses the specific trends from the REST API.
     * @author Omar Chehab
     */
    NetworkBus.getSpecificTrends = function (callback, id) {
        var endpoint = ENDPOINTS.specificTrends
            .replace(/{id}/, "" + id);
        var url = "" + APIURL + endpoint;
        window['fetch'](url)
            .then(function (response) {
            return response.json();
        }, function (error) {
            callback(error, undefined);
        })
            .then(function (response) {
            response = new specific_trends_1.SpecificTrends(response);
            callback(undefined, response);
        });
        console.log('getSpecificTrends', url);
    };
    /**
     * Requests and parses the specific content from the REST API.
     * @author Omar Chehab
     */
    NetworkBus.getSpecificContent = function (callback, id, page) {
        var endpoint = ENDPOINTS.specificContent
            .replace(/{id}/, "" + id)
            .replace(/{page}/, "" + page);
        var url = "" + APIURL + endpoint;
        window['fetch'](url)
            .then(function (response) {
            return response.json();
        }, function (error) {
            callback(error, undefined);
        })
            .then(function (response) {
            response.news = response.news.map(function (news) { return new news_1.News(news); });
            response.tweets = response.tweets.map(function (tweet) { return new tweet_1.Tweet(tweet); });
            response = new specific_content_1.SpecificContent(response);
            callback(undefined, response);
        });
        console.log('getSpecificContent', url);
    };
    return NetworkBus;
}());
exports.NetworkBus = NetworkBus;
;
;
;
